"use strict";

function Node(val) {
    this.value = val
    this.next = null
}

var getReversedList = function (head, from, to) {
    if(head == null || from > to || from < 1) return head

    var len = 0
    var fromPre = null
    var toPost = null
    var curNode = head
    while(head) {
        len++
        fromPre = (len + 1 == from) ? curNode : fromPre
        toPost = (len - 1 == to) ? curNode : toPost
    }
    if(to > len) return head

    curNode = fromPre.next
    var nextNode = curNode.next
    curNode.next = toPost
    var p = null
    while(nextNode != toPost) {
        p = nextNode.next
        nextNode.next = curNode
        curNode = nextNode
        nextNode = p
    }
    return curNode

}

export function reversePartOfLinkedList() {
    var test1 = function () {
       var head = new Node(1)
        for(var i=2;i<=5;i++) {
           head.next = new Node(i)
        }
        return head
    }()
    var from = 2, to = 4
    return
    return getReversedList(test1, from, to)
}



